import React, { useContext, useState } from "react";
import "./style.css";
import {UserContext} from "../../contexts/user";
import { db } from "../../firebase";

export default function CommentInput({comments, id}){

    const [user, setUser] = useContext(UserContext).user
    const [comment, setComment] = useState("");

    const [commentArray, setCommentArray] = useState(comments ? comments : []);
    
    const addComment = () => {
       if(comment != ""){
        commentArray.push({
            comment: comment
        })

        db.collection("posts")
        .doc(id)
        .update({
            comments: commentArray,
            username:user
        }).then(function(){
            setComment = "";
            console.log("Coment added");
        }).catch(function(error){
            console.log(`Error ${error}`);
        })
       }
    }
    return (
        <div className="commentInput">
            <textarea className="commentInput__textarea"  placeholder="type a comment" rows="1" value={comment} onChange={(e) => setComment(e.target.value)}></textarea>
            <button  onClick={addComment} className="commentInput__btn">Post</button>
        </div>
    )
}